<template>
  <div>
    <div class="container vw-100 vh-100 c-container-color col-12">
      <form class="c-position-absolute col-xl-3 col-lg-6
        col-md-8 col-sm-12 rounded shadow bg-light" @submit.prevent="logIn">
        <h4 class="mt-5 text-center font-weight-bold">員工登入</h4>
        <div class="form-group">
          <label for="email-input">使用者名稱</label>
          <input
            type="email"
            class="form-control"
            id="email-input"
            aria-describedby="emailHelp"
            placeholder="使用者名稱"
          />
        </div>
        <div class="form-group">
          <label for="password-input">密碼</label>
          <input type="password" class="form-control" id="password-input" placeholder="密碼" />
        </div>
        <div class="form-group form-check">
          <input type="checkbox" class="form-check-input" id="remember" />
          <label class="form-check-label" for="remember">記住我</label>
        </div>
        <button type="submit" id="btn-login"
          class="btn btn-primary mb-5">登入</button>
      </form>
    </div>
  </div>
</template>

<script>

export default {
  methods: {
    logIn() { // 暫存密碼在瀏覽器，確認護士(Id or email)
      const vm = this;
      const email = document.getElementById('email-input').value;
      const password = document.getElementById('password-input').value;
      localStorage.setItem('email', JSON.stringify({ email: email }));
      if (email || password) {
        vm.$store.dispatch('loadingOverlay');
        setTimeout(() => {
          vm.$store.dispatch('loadingOverlay');
          vm.$router.push({ path: 'panel' });
        }, 750);
      } else {
        // eslint-disable-next-line
        alert('請輸入信箱及密碼');
      }
    },
  },
};
</script>
